import { User, LoginDetail } from "@/app/types/User";
import { USER_URL } from "@/utils/API_URL";

export const userServices = {
    register: async (newUser: User) => {
        //validate newUser
        //try create new user
        const config = {
            method: "POST",
            headers: { "content-type": "application/json" },
            body: JSON.stringify(newUser),
        };
        try {
            const res = await fetch(USER_URL, config);
            if (res.ok) {
                alert("ok");
                const data = await res.json();
                console.log(data);
            } else {
                alert("not ok");
            }
        } catch (error) {
            console.log(error);
        }
    },
    login: async (loginDetail: LoginDetail) => {
        console.log(loginDetail);
    },
};
